/* api.openweathermap.org/data/2.5/weather?q={city name}&appid={65c4f2937e1a9bfe2445cc1092327f40} */

*{
    padding: 0;
    margin:0;
    font-family:Arial, Helvetica, sans-serif;
}

body{
    background: #f3f2ef;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family:Arial, Helvetica, sans-serif
}
html{
    font-size: 100%;
    height: 100%;
}
html{
    background: #eee;
}
.box{
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0,0,0,0.2);
    background: #a5bbdd;
    position: relative;
    overflow: hidden;
    transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 35rem;
}
.wave{
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 30000ms infinite linear;
    z-index: 1;
}
.wave.-three{
    animation: drift 50000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave.-two{
    animation: drift 70000ms infinite linear;
    z-index: 3 !important;
    opacity: 0.1;
}
.box::after{
    content: "";
    display: block;
    left: 0;
    top:0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d();
}
@keyframes drift{
    from{
        transform: rotate(0deg);
    }
    from{
        transform: rotate(3600deg);
    }
}
.info{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 60vh;
    z-index: 4;
}
.location{
    margin-top: -5.5rem;
    text-align: center;
    font-weight: 500;
    font-size: 5rem;
    text-transform: capitalize;
}
.fa-street-view{
        animation: rotates 3s linear infinite alternate;
}
@keyframes rotates{
    from{
        transform: translateX(-0.5rem);
    }
    to{
        transform: translateX(0.5rem);
    }
}
#date{
    text-align: center;
    margin-top: 0.5rem;
    color: #57606f;
    font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
}
.temp{
    margin-top: 2.5rem;
    text-align: center;
    font-size: 3rem;
}
.tempmin_max{
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 300;
    font-size: 1.2rem;
    color: #57606f;
}
#weathercon{
    height: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
}
#weathercon .fas{
    font-size: 6rem;
    animation: fas-anime 3s linear infinite alternate;
}
@keyframes fas-anime{
    from{
        transform: scale(1.1);
    }
    to{
        transform: scale(1.5);
    }
}
body>span {
    width: 100vw;
    text-align: center;
    color: grey;
}
.inputData{
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;    
}
.inputField{
    width: 50%;
    margin:auto;
    margin-top: 10px;
    border-radius: 5rem;
    padding: 1rem 1rem;
    outline:none;
    font-size: 1.5rem;
}
.errorMsg{
    text-align: center;
    font-size: 2rem;
    margin:2rem auto;
}